.page_blog {
    .sect_nav {
        ul {
            li {
                margin-right: rem(18);
                &:nth-child(4n) {
                    margin-right: 0;
                }
            }
        }
    }
    .articles {
        padding: rem(36) 0 0 0;
        &__items {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .page_nav {
        margin-top: 0;
        padding: rem(48) 0 rem(64) 0;
    }
}

.page_blog.position_scroll {
    position: relative;
    &::before {
        content: "";
        height: rem(117);
        width: 100%;
        display: block;
    }
}

.default_subscribe {
    .subscribe__form {
        margin-bottom: rem(52);
        .title {
            font-weight: 500;
            display: block;
            margin-bottom: rem(8);
        }
        .field {
            border-color: #E5E8EC;
            width: 75%;
            padding-right: rem(72);
        }
        .button_secondary {
            position: absolute;
            top: 0;
            right: 0;
            padding: rem(11) rem(30);
        }
    }
    .form_send_success {
        .field_set {
            opacity: 0;
        }
        .form_success {
            top: rem(24);
            display: flex;
            p {
                color: var(--c-dark);
            }
            button {
                i {
                    color: var(--c-dark);
                    &::before {
                        color: var(--c-dark);
                    }
                }
            }
        }
    }
}